import React from 'react';
const valineOptions = [
    'el',
    'appId',
    'appKey',
    'placeholder',
    'path',
    'pure',
    'avatar',
    'meta',
    'pageSize',
    'lang',
    'langMode',
    'visitor',
    'highlight',
    'avatarForce',
    'recordIP',
    'serverURLs',
    'emojiCDN',
    'emojiMaps',
    'enableQQ',
    'requiredFields',
];
const buildValine = async (options) => {
    const RealValine = (await import('valine')).default;
    new RealValine(options);
};
const buildPureValine = async (options) => {
    const RealValine = (await import('valine/dist/Valine.Pure.min')).default;
    new RealValine(options);
};
/**
 * Valine component
 * @returns React Element
 */
export default function Valine(props) {
    const ref = React.useRef(null);
    const others = {};
    for (const key in props) {
        if (!valineOptions.includes(key)) {
            others[key] = props[key];
        }
    }
    React.useEffect(() => {
        const options = { ...window.valineOptions, ...props };
        if (ref.current) {
            if (options.pure) {
                buildPureValine({
                    ...options,
                    el: ref.current,
                });
            }
            else {
                buildValine({
                    ...options,
                    el: ref.current,
                });
            }
        }
    }, [props, ref]);
    // other props will transfer to div
    return React.createElement("div", { ref: ref, ...others });
}
